import React,{Component} from "react";
import { Container,Nav,Navbar,NavDropdown } from "react-bootstrap";
import { Route,Link,NavLink } from "react-router-dom";
// import '../style.css'
import style from '../insure.module.css'
class Header extends Component{
render(){
    return(
      <div>
            {/* ------------------------navbar------------------------ */}
            <Navbar collapseOnSelect expand="lg" id="Navbar1" className="bg-body-tertiary  fixed-top shadow Navbar1">
               <Container>
                    <Navbar.Brand href="#home" className="img-fluid me-5">
                        <img src="./images/logo.png" className={`me-3 ${style['logo']}`} alt=""/>
                        <span className={`${style.logotxt}`}>Insure</span>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mx-auto">
                        <Link reloadDocument className={`px-2 nav-link active`} to='/'><b className={`px-2  ${style.navlink}`}>Home</b></Link>
                        <Link reloadDocument className={`px-2 nav-link active`} to='/about'><b className={`px-2  ${style.navlink}`}>About Us</b></Link>
                        <Link reloadDocument className={`px-2 nav-link active`} to='/services'><b className={`px-2  ${style.navlink}`}>Our Services</b></Link>
                        <NavDropdown  className={`px-3 bold ${style.navlink}`} title="Pages" id="collapsible-nav-dropdown">
                          <NavDropdown.Item href=""><Link  className={`nav-link active`} to='/features'> Features</Link></NavDropdown.Item>
                          <NavDropdown.Item href=""><Link  className={`nav-link active`} to='/appointment'>Appointment</Link></NavDropdown.Item>
                          <NavDropdown.Item href=""><Link  className={`nav-link active`} to='/team'>Team Members</Link></NavDropdown.Item>
                          <NavDropdown.Item href=""><Link  className={`nav-link active`}to='/testimonial'>Testimonial</Link></NavDropdown.Item>
                        </NavDropdown>
                        <Link reloadDocument className={`px-2 nav-link active`} to='/contact'><b className={`px-2  ${style.navlink}`}>Contact Us</b></Link>
                    </Nav>
                    <button className={`px-2 ${style.navbtn}`}>Get A Quote</button>
                    </Navbar.Collapse>
                </Container> 
            </Navbar>
            {/*----------------------- navbar end---------------------- */}
            </div>
        )
    }
}
export default Header