import React,{Component} from "react";
// import { Container,Row,Col,Breadcrumb,Card } from "react-bootstrap";
import style from "../insure.module.css";

class Scrollup extends Component{
    constructor(){ 
        super(); 
     
        this.state = { 
          scrolled: false, 
        }; 
      } 
     
      componentDidMount() { 
        window.addEventListener('scroll', this.handleScroll); 
      } 
     
      componentWillUnmount() { 
        window.removeEventListener('scroll', this.handleScroll); 
      } 
     
      handleScroll = () => { 
        if (window.pageYOffset > 600) { 
          this.setState({ scrolled: true }); 
        } else { 
          this.setState({ scrolled: false }); 
        } 
      };

      scrollToTop = () => {
        window.scrollTo({
          top: 0,
          behavior: 'smooth',
        });
      };

    render(){
        const { scrolled } = this.state;
        return(
            <div>
                {/* <h2 className={style.po}>
                    jhdss
                </h2> */}
                <span className={style.up}>
                <a href="#" onClick={this.scrollToTop} className={`btn btn-lg  ${scrolled ? `btn-primary animate__animated animate__fadeInTopRight`:`d-none ` } btn-lg-square ${style.backtotop}`}><i className="fa-solid fa-arrow-up-long text-light animate__animated animate__zoomIn animate__slow  animate__infinite infinite"></i></a>
                </span>
                {/* className={`navbar fixed-top navbar-expand-lg ${scrolled ? 'navbar-light bg-light shadow' : 'navbar-dark'  */}
            {/* } p-md-3`}>  */}

            </div>
        )
    }
}export default Scrollup
